$sans-font-stack: Verdana, Arial, Helvetica, sans-serif;
$full-screen-sidenav-allotment: 200px;
$mobile-screen-sidenav-allotment: 40px;
$header-height: 50px;
$thin-space: 5px;
$med-space: 10px;

$cornflower: #4459e2;
$pale-cornflower: #8bb1ef;
$deep-cornflower: #224d93;
$light-gray: #ccc;
$smallscreen: 768px;

* { 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; 
}
html { background: white; }
body { 
  font-family: $sans-font-stack; 
  // Body has a default margin. Override.
  margin: 0px; 
}

.sidenav {
  height: 100%;
  width: $full-screen-sidenav-allotment;
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  display: block;
  background-color: $light-gray;
  box-sizing: border-box; 
  transition: 0.3s;

 
}

.entry {
 a {
    padding: 4px 8px 4px 20px;
    font-size: 120%;
    text-decoration: none;
    display: block;
    color: $cornflower;
  }

  a:hover {
      background-color: $pale-cornflower;
  }
}

.sidenav-header{
  height: $header-height + $thin-space;
  padding-left: $med-space;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;

  a {
    padding: 0;
    color: black;
    text-decoration: none;
  }

  a:visited {
    color: black;  
  }  

}

.page-header{
  height: $header-height;
  background-color: $pale-cornflower;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;
  padding-left: $med-space;
}
.page-content{
  padding-left: $med-space;
  // padding-right: $thin-space;
  // padding-bottom: 0;
}


.sidenavburger {
    // width: $mobile-screen-sidenav-allotment;
    width: 30px;

    font-size:20px!important;
    text-align: center;
    line-height: 30px;

    position: fixed;
    left: 5px;
    top: 5px;
    z-index: 2;

    display: inline;

    border-radius: 5px;
    background-color: $light-gray;
    border-style: solid;
    border-width: 2px;
}

.burger-background {
    width: $mobile-screen-sidenav-allotment;
    height: $header-height;

    position: absolute;
    left: 0px;
    top: 0px;

    background-color: $pale-cornflower;
}





.main-body {
   // Clicks via 'onclick' make the screen flash on Safari mobile without this.
  -webkit-tap-highlight-color: transparent;

  a {
    color: $cornflower;
    text-decoration: none;
  }

  a:visited {
    color: $deep-cornflower;  
  }  
}


@media (max-width: $smallscreen - 1px){
  .sidenav{ width: 0; }
  .sidenav.showing{ width: $full-screen-sidenav-allotment }
  .main-body {
    margin-left: $mobile-screen-sidenav-allotment;
  }
  .page-header{
    padding-right: 5px
  }
  .page-content{
    padding-right: 5px
  }
}

@media (min-width:$smallscreen){
  .hide-large{display:none!important}
  .sidenav{ width: $full-screen-sidenav-allotment; }
  .main-body {
    margin-left: $full-screen-sidenav-allotment; 
  }
  .page-header{
    padding-right: 20px
  }
  .page-content{
    padding-right: 20px
  }
}




.table-box{
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;  
  margin-right: 5px;
  margin-bottom: 5px;
}

table.asst-table {
    border: 2px solid black;
    border-collapse: separate;
    border-spacing: 20px 10px;

    td {
      white-space:nowrap;
      word-break:break-all;
    }
}

dt {
  float: left;
  width: 30px;
  text-align: right;
  font-weight: bold;
}

dd {
  margin: 0 0 0 60px;
  padding: 0 0 0.5em 0;
}
